  --fs-1: 2rem;
  --fs-2: 1.5rem;
  --fs-3: 1rem;
  --icon-1: 3rem;
  --icon-2: 2rem;
  --icon-3: 1.5rem;
  --box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.7);
  --accent-color: rgb(92, 168, 92);
  --bg-lvl-1: #121212;
  --bg-lvl-2: rgba(255, 255, 255, 0.05);
}

.dark-theme {
  background-color: var(--bg-lvl-1);
  color: white;
}

.icon-1 {
  display: block;
  height: var(--icon-1);
  width: var(--icon-1);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.icon-2 {
  display: block;
  height: var(--icon-2);
  width: var(--icon-2);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.icon-3 {
  display: block;
  height: var(--icon-3);
  width: var(--icon-3);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 0;
}

.icon-dark {
  background-color: white;
}

.icon-light {
  background-color: #121212;
}

.content {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 2em 0;
}

.header,
.main,
.footer {
  box-sizing: border-box;
  padding: 0 2rem;
}

.header {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  background-color: var(--accent-color);
  transform: scaleY(2) translateY(-35.5%) skewY(-7deg);
  width: 100vw;
  box-shadow: var(--box-shadow);
}

.header-container {
  display: block;
  max-width: 1000px;
  transform: translateY(35.5%) skewY(7deg) scaleY(0.5);
}
.header-image-container {
  width: 35%;
}
.header-image {
  float: left;
  margin-right: 1.5rem;
  box-shadow: var(--box-shadow);
  max-width: 300px;
  width: 50%;
  z-index: 1;
}

.header-background {
  margin-top: 1.5rem;
  margin-left: 1.5rem;
  box-sizing: border-box;
  padding: 1.5rem;
  grid-area: content;
  display: block;
  flex-direction: column;
  justify-content: flex-start;
  box-shadow: var(--box-shadow);
}

.header-background::after {
  background-color: var(--bg-lvl-2);
  z-index: 2;
}

.header-background.dark-theme::before {
  z-index: 1;
}

.header-title {
  align-self: flex-start;
  font-size: var(--fs-1);
}

.header-info {
  margin: 1.5rem 0;
  font-size: var(--fs-2);
  line-height: 2rem;
  width: 100%;
  align-self: center;
  text-align: left;
}

.header-buttons {
  align-self: flex-end;
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}

.github {
  mask-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.linkedin {
  mask-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.twitter {
  mask-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.live-preview {
  mask-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

.main {
  margin: 6rem 0;
  display: flex;
  justify-content: center;
}

.section {
  flex-grow: 1;
  max-width: 1000px;
}

.project-section-title {
  font-size: var(--fs-1);
  width: 100%;
  text-align: left;
  margin-bottom: 2rem;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.project-card {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-lvl-2);
  box-shadow: var(--box-shadow);
}

#battleship-image,
#ssb-image {
  transform-origin: left bottom;
}

#weather-image,
#restaurant-image {
  transform-origin: bottom;
}

#todo-image,
#library-image {
  transform-origin: right bottom;
}

.screenshot {
  width: 100%;
  transition: transform ease-out 0.2s;
}

.screenshot:hover {
  transform: scale(2);
}

.project-info {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
  box-sizing: border-box;
  padding: 1.5rem;
}

.project-title {
  font-size: var(--fs-2);
  justify-self: start;
}
.project-buttons {
  display: flex;
  gap: 1rem;
  justify-self: end;
}

.project-description {
  font-size: var(--fs-3);
  grid-column: 1 / 3;
  line-height: 1.25rem;
}

.footer {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-color: var(--accent-color);
  transform-origin: top;
  transform: translateY(4rem);
}

.footer-container {
  transform: translateY(-4rem);
  display: flex;
  max-width: 1000px;
}

.footer-info {
  flex-grow: 1;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
  box-shadow: var(--box-shadow);
}

.footer-info::after {
  background-color: var(--bg-lvl-2);
  z-index: 2;
}

.footer-info.dark-theme::before {
  z-index: 1;
}

.footer-title {
  font-size: var(--fs-1);
}

.footer-desc {
  font-size: var(--fs-3);
  line-height: 1.5rem;
  margin-right: 2rem;
}

.address {
  font-size: var(--fs-3);
}

.contact-container {
  display: flex;
  gap: 1rem;
}

.contact-info {
  font-size: var(--fs-3);
}

.footer-buttons {
  display: flex;
  gap: 1rem;
}

.footer-image-container {
  align-self: center;
  transform: translateX(-2rem);
}

.footer-image {
  max-height: 80%;
  max-width: 200px;
  box-shadow: var(--box-shadow);
}

@media (max-width: 1000px) {
  #battleship-image,
  #todo-image,
  #restaurant-image {
    transform-origin: left bottom;
  }
  #weather-image,
  #ssb-image,
  #library-image {
    transform-origin: right bottom;
  }

  .card-container {
    grid-template-columns: repeat(2, 1fr);
  }

  .header-container {
    display: block;
  }
}

@media (max-width: 500px) {
  .content {
    padding: 1.5em 0;
  }
  .header,
  .main,
  .footer {
    box-sizing: border-box;
    padding: 0 1.5rem;
  }
  .main {
    margin: 4rem 0;
  }
  .header-container {
    display: flex;
    flex-direction: column;
  }

  .header-background {
    margin: 0;
    transform: translateY(-2rem);
    z-index: 0;
  }
  .header-title {
    margin-top: 3.5rem;
  }
  .header-image-container {
    display: block;
    width: 80%;
    align-self: center;
    z-index: 1;
  }
  .header-image {
    max-width: none;
    display: block;
    transform: translateY(1rem);
    float: none;
    margin: 0;
    width: 100%;
  }
  .card-container {
    grid-template-columns: 1fr;
  }
  .screenshot:hover {
    transform: scale(1);
  }
  .footer-container {
    flex-direction: column;
  }
  .footer-image-container {
    width: 80%;
    align-self: center;
    transform: translateX(0) translateY(-2rem);
  }
  .footer-image {
    max-width: none;
    width: 100%;
  }
  .footer-buttons {
    margin-bottom: 2rem;
  }
}
